import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="omsk"><Icon24BrowserBack /></HeaderButton>}>Омская область</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://a.d-cd.net/f80f379s-1920.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://superomsk.ru/images/news/full/2017/02/4a448b4344d0e36b6a954b00f6ecbd5e.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://user.vse42.ru/files/P_S1280x960q80/Wnone/ui-528db4eb317150.40532624.jpeg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Омская область расположена на юге Западной Сибири. На юге она граничит с Казахстаном, на западе и севере — с Тюменской областью, на востоке — с Новосибирской и Томской областями. Входит в состав Сибирского федерального округа.                </Cell>
                </Group>           
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;