import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="ufa"><Icon24BrowserBack /></HeaderButton>}>Республика Башкортостан</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://yahooeu.su/uploads/posts/2015-11/1448021581_10717407.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://content.foto.my.mail.ru/mail/nordprod7/kumertau/h-7699.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://s0.rbk.ru/v6_top_pics/media/img/7/97/754441278722977.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Республика Башкортостан расположена на востоке Европы в районе Южного Урала. По рельефу Башкортостан чётко подразделяется на три района: Предуралье, Горный (Башкирский) Урал и Зауралье.
                </Cell>
            </Group>     
            <Group title="Год основания">
                <Cell multiline>
                    1919 г.
                </Cell>
            </Group>      
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;