import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery, Link } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="spbindex"><Icon24BrowserBack /></HeaderButton>}>Дом Зингера</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://static.tonkosti.ru/images/5/57/%D0%97%D0%BD%D0%B0%D0%BC%D0%B5%D0%BD%D0%B8%D1%82%D1%8B%D0%B9_%D0%B4%D0%BE%D0%BC_%D0%97%D0%B8%D0%BD%D0%B3%D0%B5%D1%80%D0%B0_%D0%B2_%D0%A1%D0%B0%D0%BD%D0%BA%D1%82-%D0%9F%D0%B5%D1%82%D0%B5%D1%80%D0%B1%D1%83%D1%80%D0%B3%D0%B5.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-pdb/231404/a0271d70-04f6-4747-8ace-38d279e9016a/s1200?webp=false" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-pdb/1517245/85660ab7-6bd3-47ba-ab6c-928bf704fd20/s1200" /></div>
              </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про достопримечательность">
                <Cell multiline>
                Унылые слова «офисный центр» никак не ассоциируются с прекрасным зданием напротив Казанского собора в Санкт-Петербурге, известное большинству горожан как «Дом книги». Оно построено по заказу «Акционерной компании Зингер в России» в начале прошлого века. Предполагалось, что в десятках контор на площади 7000  м² сконцентрируется деловая жизнь Северной столицы. Сперва все шло по плану, но с началом Первой Мировой войны туда вселилось посольство США, а после 1917 г. — книжное издательство «Лениздат». На первом этаже открылся большой книжный магазин, и название «Дом Зингера» забылось.</Cell>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
            <center><Div><Link href="https://vk.me/about_city" target="_blank">Предложить достопримечательность</Link></Div></center>
    </Panel>
);
//<Button size="l" stretched>Достопримечательности</Button>

export default Peterburg;