import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Санкт-Петербург</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://www.stihi.ru/pics/2016/02/10/6006.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-pdb/251121/a8a8a015-c494-4bab-91b7-98bdbc3fafe6/s1200?webp=false" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-pdb/1639477/77099ab6-a6a1-4c94-9404-5bc8ba3bd24d/s1200" /></div>
              </Gallery>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Санкт-Петербург — прекрасный город на Неве!
                
                Белые ночи, разводные мосты, множество различных исторических памятных мест. Недаром Санкт-Петербург именуют Северной Венецией, и он для жителей России является ее культурной столицей.

                Для многих людей близкие и знакомые слова Невка, Фонтанка и Мойка. Изобилие каналов и ручьев просто потрясает. А ведь есть еще пруды и искусственные водоемы, а также красивые озера. 
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1703 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="lenobl">Область</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);
// <Button size="l" stretched>Достопримечательности</Button>

export default Peterburg;