import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="kostroma"><Icon24BrowserBack /></HeaderButton>}>Костромская область</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://4put.ru/pictures/max/99/304955.jpg" /></div>                        
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Кострома — древний русский город, основанный в 12 веке, входит в восьмерку «Золотого Кольца России» — исторических городов, где находится большое количество памятников древнерусской истории и культуры. Благодаря выгодному расположению на Волге — одном их главных торговых путей средневековья, вскоре после основания Кострома быстро превратилась в богатый купеческий город.                </Cell>
            </Group>          
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;