import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Нижневартовск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-pdb/27625/57523e61-21be-4357-bd89-c717af09edf3/s1200" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://img-fotki.yandex.ru/get/9319/46869660.49/0_dcfa4_325e49e5_XXL.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://pochel.ru/media/photologue/photos/ec0/ec0229c6-1f61-4d71-90dc-3de342092edd.jpg" /></div>
              </Gallery>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Нижневартовск - современный город нефтяников, живущей напряженной производственной и культурной жизнью. Он находится на правом берегу Оби, в 400 километрах от города Ханты-Мансийск. Площадь города составляет 26 421 гектар. Здесь проживают 258 780 человек различных наций и народностей. Из них 66 % русских, 14% – башкир, 11% - украинцев, 0.3 % – выходцы из Кавказа.

                Нижневартовск вошел в пятёрку самых богатых городов России, опережая даже Санкт-Петербург и Екатеринбург.
                Климат умеренный, континентальный. Лето короткое и прохладное, зима длительная и морозная. Весна и осень короткие.

                В Нижневартовске отсутствуют какие-либо административные районы, он поделен на жилые микрорайоны, кварталы и поселки.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1909 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} disabled>Область</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);
// <Button size="l" stretched>Достопримечательности</Button>
export default Peterburg;