import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Sochi = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="kur"><Icon24BrowserBack /></HeaderButton>}>Сочи</PanelHeader>
        <Group>
        <center>
            <Div>
            <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-zen_doc/965902/pub_5d60fd0a2f1e4409e4e3f194_5d60ffe8ecfb8000ad173b55/scale_1200" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://sochi24.tv/sites/default/files/images/zametka_files/sochi_10.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://sochi-express.ru/wp-content/uploads/2019/08/567f9abed9ce9d527d4badb5ec5bb310.jpg" /></div>
              </Gallery>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Сочи – это курортный город на юге России. Он расположен на черноморском побережье, и является частью Краснодарского края. На сегодняшний день — это самый крупный город-курорт на всей территории страны, и, возможно, потому он признан экономическим центром побережья.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1838 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="kras_sochi">Область</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Sochi;