import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="port"><Icon24BrowserBack /></HeaderButton>}>Новороссийск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://sun9-51.userapi.com/c854024/v854024463/132c31/4nrhzdurNdg.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://mtdata.ru/u23/photo96E0/20796107861-0/original.jpeg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://webcam-krasnodar.ru/Foto/Novorossiysk/Novorossiysk_foto_13B.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Город Новороссийск — морской курорт, расположенный на юге России, входит в состав Краснодарского края. Население города составляет 251013 человек. Новороссийск является важным транспортным центром, т.к в городе находится крупнейший в России Черноморский порт.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1838 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="kras_novor">Область (край)</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;