import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="novorossiysk"><Icon24BrowserBack /></HeaderButton>}>Краснодарский край</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://os1.i.ua/3/1/13196214_cad56617.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://s47.radikal.ru/i117/1106/19/eae574138580.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://s2.travelask.ru/system/images/files/001/024/155/wysiwyg/002.jpg?1516627510" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Краснодарский край образован 13 сентября 1937 г. в результате разделения Азово-Черноморского края на Ростовскую область и Краснодарский край с терриорией 85 тыс. кв. км (с Адыгейской автономной областью).
                </Cell>
            </Group>        

            <Group title="Год основания">
                <Cell multiline>
                    1937 г.
                </Cell>
            </Group>   
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;