import React from 'react';
import { Panel, PanelHeader, Group, Cell, Epic, Tabbar, TabbarItem, List, HeaderButton } from "@vkontakte/vkui";
import Icon24Home from '@vkontakte/icons/dist/24/home';
import Icon24Story from '@vkontakte/icons/dist/24/story';
import Icon24Attachments from '@vkontakte/icons/dist/24/attachments';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

let activeColor = "#2975cc";

const dostopr = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="peterburg"><Icon24BrowserBack /></HeaderButton>}>Acity</PanelHeader>
        <Group title="Выберите достопримечательность" description="Достопримечательности Санкт-Петербурга">
            <List>
                <Cell before={<Icon24Attachments />} expandable onClick={go} data-to="spbzinger">
                    Купол Зингера
                </Cell>
            </List>        
        </Group>
    </Panel>
);

export default dostopr;