import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="perm"><Icon24BrowserBack /></HeaderButton>}>Пермский край</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://druza.web.ru/l-Perm-kr_DSC01779.JPG" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://img.lookmytrips.com/images/look7905/image-56f19284ff936738eb0e68b7-5718f883dae1c-1bhhu43.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://a.d-cd.net/c8fb7fu-960.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Пермская область является одним из ведущих регионов Российской федерации. На ее территории находятся крупнейшие предприятия по производству калийных солей и химической продукции. Градообразующими являются машиностроительные и металлургические заводы, целлюлозно-бумажные фабрики. Все они активно развиваются и предоставляют рабочие места. Города этого региона не похожи друг на друга. Одни из них являются крупными промышленными центрами, например Березники и Соликамск, другие же находятся в экономически депрессивном состоянии.
                </Cell>
            </Group>           
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;