import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="port"><Icon24BrowserBack /></HeaderButton>}>Находка</PanelHeader>
        <Group>
        <center>
            <Div>
            <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://sun9-66.userapi.com/c854024/v854024020/138124/cFRVZcDZASU.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://turist-dv.ru/wp-content/uploads/2015/12/0_1338f3_ef7b6ae3_XXL.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://turist-dv.ru/wp-content/uploads/2015/12/0_133b7d_ebf3cc9_XXL.jpg" /></div>
              </Gallery>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Находка является городом Приморского края Российской Федерации, который расположен на самом юге страны и располагается на берегах Японского моря. Много людей называют его еще как “ворота в Тихий океан”.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1864 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="primobl">Область (край)</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;