import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="peterburgPort"><Icon24BrowserBack /></HeaderButton>}>Ленинградская область</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://a.d-cd.net/fQAAAgL5yOA-1920.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://www.syl.ru/misc/i/ai/381008/2475438.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://www.turizmvnn.ru/files/system/foto/5752473.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Площадь Ленинградской области – 83900 квадратных километров, это 0,5 % от площади России. Ее протяженность с юга на север – 320 километров, с востока на запад – 500 километров. По своей форме Ленинградская область напоминает трилистник. Граничит с двумя странами: Финляндией и Эстонией, а также с регионами: Псковской, Новгородской, Вологодской областями и Республикой Карелией. 
                </Cell>
                </Group>

            <Group title="Год основания">
               <Cell>1927 г.</Cell>
            </Group>            
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;