import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Сургут</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://pilothub.ru/datas/folio/1267-gazprom-transgaz-surgut.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://pics2.pokazuha.ru/p217/i/8/10001166u8i.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://gulaytour.ru/wp-content/uploads/2017/11/0_9196b_8533f82b_orig-e1510659059255.jpeg" /></div>
              </Gallery>       
              </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Сургут – российский город, расположенный в Тюменской области, является административным центром Ханты-Мансийского АО (автономного округа). Самый крупный город в округе, крупнейший, находящийся в Приобье, речной порт. Важный узловой центр автомобильного и железнодорожного сообщения. Имеет международный аэропорт.

                Сургут считается неофициальной столицей по нефтедобыче в России. Своё название "Сургут" город получил из-за близкого расположения обской протоки Сургут-Мухэт. С1965-го года является городом, до этого был селом.

                Численность населения города Сургут - 308, 1 тысяч жителей (январь 2011г.). Ежегодный прирост населения составляет более двух тысяч человек в год.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1594 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="tumobl">Область</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);
//<Button size="l" stretched>Достопримечательности</Button>

export default Peterburg;