import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Пермь</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://pbs.twimg.com/media/D9u271RW4AA5gMI.jpg:large" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://cdn.fishki.net/upload/post/201602/25/1861447/cs3xnxqy3n0.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://a.d-cd.net/ec0f901s-1920.jpg" /></div>
              </Gallery>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                    Центр Пермского края, город Пермь находится в 1100 километрах, восточнее Москвы. В городе проживает около одного миллиона жителей. Пермь - третий по площади город России. Площадь города - примерно 800 квадратных километров. Пермь тянется вдоль реки Камы на 70 километров. Среднегодовая температура воздуха в городе: +2,3 С.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1723 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="permobl">Область (край)</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);
//<Button size="l" stretched>Достопримечательности</Button>

export default Peterburg;