import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="chelabinsk"><Icon24BrowserBack /></HeaderButton>}>Челябинская область</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://www.dentalcollege.ru/upload/iblock/b41/73chelyab_0.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://bfm74.ru/upload/resize_cache/iblock/abe/1024_768_2/abeeca7f21933da33cec73b5d5bbb492.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://a.d-cd.net/caec552s-960.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Челябинская область входит в состав Уральского федерального округа и  занимает площадь в 88,5 тыс. кв. км,  простираясь с юга на север на 490 км, с запада на восток - на 400 км. Челябинская область находится условных границах двух частей света - Европы и Азии: горной - около 150 км на Урал-тау и Уральскому хребту и водной - около 220 км по реке Урал. Общая протяженность границ Челябиснкой области составляет 2750 км.
                </Cell>
            </Group>        

            <Group title="Год основания">
                <Cell multiline>
                    1736 г.
                </Cell>
            </Group>   
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;