import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery, Link } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="mosindex"><Icon24BrowserBack /></HeaderButton>}>Московский Кремль</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://pbs.twimg.com/media/DxVRYT7XQAAeP9b.jpg:large" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://ak4.picdn.net/shutterstock/videos/11669384/thumb/1.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://live.staticflickr.com/65535/46891286875_d4f49abef8_b.jpg" /></div>
              </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про достопримечательность">
                <Cell multiline>
                Московский кремль — крупная крепость, расположенная на Боровицком холме в российской столице — Москве. С давних времен является градообразующим, историческим, политическим и культовым центром города. Сегодня здесь находится официальная резиденция президента Российской Федерации. В 1991 на базе Государственных музеев Московского Кремля был образован историко-культурный музей-заповедник. Сейчас кремль является главным центром притяжения у туристов, посещающих московскую столицу.
                </Cell>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
            <center><Div><Link href="https://vk.me/about_city" target="_blank">Предложить достопримечательность</Link></Div></center>
    </Panel>
);
//<Button size="l" stretched>Достопримечательности</Button>

export default Peterburg;