import React from 'react';
import { Panel, PanelHeader, Group, Cell, Button, Div, HeaderButton, Gallery } from '@vkontakte/vkui';
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Kostroma = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="kur"><Icon24BrowserBack /></HeaderButton>}>Кострома</PanelHeader>       
        <Group>
            <center>
                <Div>
                    <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://russia.travel/upload/slider/8ba/8ba7d7bf9cead6c60893924331646d5d.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://images.inlearno.ru/events_preview/b9f182c3e58b500b9fd60d91dccda0e0.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://кострома100.рф/files/pics/pics17200.jpg" /></div>
                    </Gallery> 
                    </Cell>   
                </Div>   
            </center>    
        </Group> 

        <Group title="Про город">
            <Cell multiline>
                Кострома является городом с незабываемыми достопримечательностями, а также административным, промышленным и культурным центром Костромской области. Расположен он на Костромской низменности, к северо-востоку от столицы России — Москва, у старого устья реки Костромы и на обоих берегах реки Волги.
            </Cell>
        </Group>

        <Group title="Год основания">
            <Cell multiline>1152 г.</Cell>
        </Group>

        <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="kostobl">Область</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Kostroma;