import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Yalta = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="kur"><Icon24BrowserBack /></HeaderButton>}>Ялта</PanelHeader>
        <Group>
        <center>
            <Div>
            <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://prekrasnij-mir.ru/uploads/users/user_91/yalta-city-twilight_35068_990x742.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-altay/1871013/2a000001699f50e5dfc2ebd4fb8f6db7e3fb/XXL" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-altay/1618837/2a000001699f50f598cb9c60e7a26e9f6b34/XXL" /></div>
              </Gallery>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
Ялта располагается на юге Крымского полуострова на побережье Чёрного моря. При этом разделяются понятия город Ялта и Большая Ялта. Последним называют Ялтинский регион. Ялта считается признанной столицей крымских курортов, а также торговым и пассажирским морским портом Южного берега Крыма. Распространенной версией появления названия города является, что оно имеет тюркское происхождение. Впервые упомянута Ялта была у арабского историка Аль-Идрисии в 1154 году.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1154 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="krym">Область (республика)</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Yalta;