import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Омск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://mtdata.ru/u8/photoB113/20760688673-0/original.jpeg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-pdb/1932994/1b0f5aeb-9eb8-4868-b513-f17e8d52a0f0/s1200" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://expertology.ru/upload/iblock/dab/9876543223456777.jpg" /></div>
              </Gallery>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                Омск был основан казачьим отрядом в 1716 году и изначально выполнял роль пограничной крепости, возведенной для защиты Российской Империи от набегов кочевников. Долгое время Омск служил острогом. В XIX веке Омск имел большое значение для страны и являлся центром сначала Западно-Сибирского, а затем Степного генерал-губернаторства. В годы гражданской войны Омск был столицей Российского государства и Белой России, в честь чего даже имел неофициальное название «Третья столица».

                В советские годы Омск значительно расширился за счет объединения с близлежащими городами и населенными пунктами. Сегодня город активно развивается, строится новое жилье, реконструируются дороги, растет количество населения. Омск считается одним из самых привлекательных российских городов для развития бизнеса.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1716 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="omskobl">Область</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);
//<Button size="l" stretched>Достопримечательности</Button>

export default Peterburg;