import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="moscow"><Icon24BrowserBack /></HeaderButton>}>Московская область</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://mosreg.ru/upload/gallery/167/181667_1bc8e67ff116983ac4a098199188c3d8b964ece8.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://pbs.twimg.com/media/CR_eSCXWoAAupFU.jpg:large" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://inkrasnogorsk.ru/upload/page/113/20613_picture_3c66afbfa449e7da4800fcd6532768b734ed3ee7.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Территория современной Московской области – расположенная в центральной части Восточно-Европейской равнины в бассейнах рек Волги, Оки, Клязьмы и Москвы-реки – по данным археологии  была заселена человеком 20 тысяч лет назад. Первобытное общество здесь жило охотой, собирательством и рыболовством.                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1929 г.</Cell>
            </Group>            
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;