import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="yalta"><Icon24BrowserBack /></HeaderButton>}>Республика Крым</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://photos.wikimapia.org/p/00/04/63/40/58_big.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="http://i.mycdn.me/i?r=AzEPZsRbOZEKgBhR0XGMT1RkVtz9twuAONvCMwytIbHku6aKTM5SRkZCeTgDn6uOyic" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://a.d-cd.net/9ed9769s-960.jpg" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Крымский полуостров – достаточно известное место отдыха еще со времен Российской империи. Недаром здесь возводили дворцы императоры и знать. Началось все еще в 19 веке, когда по настоянию доктора С.П. Боткина Крым был выбран императрицей Марией Александровной для лечения. А незадолго до этого в городе Саки открылась первая официальная грязелечебница.
В советские времена в Крыму активно строились пансионаты и дома отдыха для трудящихся и интеллигенции. Полуостров называли всесоюзной здравницей. Сюда на отдых приезжали жители всех 15 республик. Сама природа создала здесь все условия для отдыха и оздоровления: уникальный климат, целебный воздух, больше ста минеральных источников, лечебные грязи. Здешние курорты пользовались огромной популярностью.                
                </Cell>
            </Group>        
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;