import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Peterburg = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="prom"><Icon24BrowserBack /></HeaderButton>}>Норильск</PanelHeader>
        <Group>
        <center>
            <Div>
                <Cell>
                <Gallery
                slideWidth="90%"
                style={{height: "175px", width: "350px"}}
                bullets="dark"
              >
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-zen_doc/57035/pub_5d0765583ad2340d4ce53a5f_5d077554cf474f0da0399bbe/scale_1200" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://postupi.online/images/images1366/49/988.jpg" /></div>
                <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://newslab.ru/images/2018/Vvv/QUvBjHpSW1g.jpg" /></div>
              </Gallery>
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про город">
                <Cell multiline>
                    Норильск — это город в северной части Красноярского края, распологается к югу от полуострова Таймыр, приблизительно в 90 км восточнее Енисея. Норильск является одним из наиболее экологически загрязнённых городов во всём мире. Экологическая опасность населению города и близлежащих районов обусловливается тем, что Норильск является «производной» градообразующего комплекса. Аналогов такого крупного промышленного района «Норильск» больше нигде в мире не имеется.
                </Cell>
            </Group>

            <Group title="Год основания">
               <Cell>1935 г.</Cell>
            </Group>
            
            <Group>
            <Div style={{display: 'flex'}}>
                <Button size="l" stretched level="secondary" style={{ marginRight: 5 }} onClick={go} data-to="norobl">Область (край)</Button>
            </Div>
            </Group>
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);
//<Button size="l" stretched>Достопримечательности</Button>
export default Peterburg;