import React from 'react';
import { Panel, PanelHeader, Group, Cell, Div, HeaderButton, Button, Gallery } from "@vkontakte/vkui";
import Icon24BrowserBack from '@vkontakte/icons/dist/24/browser_back';

const Moscow = ({ id, go }) => (
    <Panel id={id}>
        <PanelHeader left={<HeaderButton onClick={go} data-to="norilsk"><Icon24BrowserBack /></HeaderButton>}>Норильский край</PanelHeader>
        <Group>
        <center>        
            <Div>
            <Cell>
                    <Gallery
                      slideWidth="90%"
                      style={{height: "175px", width: "350px"}}
                      bullets="dark"
                    >
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://s0.rbk.ru/v6_top_pics/media/img/8/18/754441511579188.jpg" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://pbs.twimg.com/media/DmlxDavW4AApFi9.jpg:large" /></div>
                        <div><img style={{ height: "150px", width: "300px", borderRadius: "25px"}} src="https://avatars.mds.yandex.net/get-zen_doc/40456/pub_5b7ae3269afa3c00ab0442ca_5b7ae6204715cb00a8fc1d56/scale_1200" /></div>
                    </Gallery> 
                </Cell>
            </Div>
            </center></Group>

            <Group title="Про область">
                <Cell multiline>
                Норильск расположен в 300 км к северу от Северного Полярного Круга, в 2400 км от Северного Полюса. До столицы Красноярского края – 1500 км. Норильск -  один из пяти самых северных городов мира с численностью населения более 100 тысяч человек. В пятерку самых северных входят также российский Мурманск, норвежский Хаммерферст, Анкоридж на Аляске и столица финской Лапландии город  Рованиеми.
                </Cell>
            </Group>        

            <Group title="Год основания">
                <Cell multiline>
                    1935 г.
                </Cell>
            </Group>   
            <center><Div style={{color: "grey"}} multiline unselectable>Вся информация взята из свободных источников.</Div></center>
    </Panel>
);

export default Moscow;